<template>
  <div>
    <div>
      <input placeholder="ID" type="text" v-model="userId"/>
    </div>
    <div>
      <input placeholder="PW" type="password" v-model="userPw" @keyup.enter="loginClick"/>
    </div>
    <div>
      <button @click="loginClick">로그인</button>
    </div>
  </div>
</template>

<script>
export default {
  name : "LoginComponent",
  data() {
    return {
      userId : "",
      userPw : ""
    }
  },
  methods : {
    loginClick() {
      if(this.userId == "") {
        alert("ID를 입력하세요");
        return;
      }

      if(this.userPw == "") {
        alert("PW를 입력하세요");
        return;
      }

      const tmpUsers = JSON.parse(localStorage.getItem("users"));

      for(let i=0; i<tmpUsers.length; i++) {
        if(tmpUsers[i].id == this.userId && tmpUsers[i].password == this.userPw) {
          const loginedUser = {
            "id" : this.userId,
            "name" : tmpUsers[i].name,
            "authority" : tmpUsers[i].authority
          };

          localStorage.setItem("loginedUser", JSON.stringify(loginedUser));
          break;
        }
      }

      if(localStorage.getItem("loginedUser") != undefined && localStorage.getItem("loginedUser") != null) {
        alert("로그인 성공!");
        this.$emit("loginSOF");
      } else {
        alert("다시 시도하세요");
      }
    }
  },
}
</script>

