<template>
  <!-- <img alt="Vue logo" src="./assets/img/logo.png" width="100"> -->
  <h3>
    Vue.js - 메뉴 보이기 연습
  </h3>
  <div class="header">
    <HeaderComponent
      :isLogined="isLogined"
      :menus="menus"
      @logout="logoutFunc">
    </HeaderComponent>
  </div>
  <div class="middle">
    <div class="menu">
      <MenuComponent
        :menus="menus"
        :isLogined="isLogined"
        :padding="10"
        style="padding-top: 20px;">
      </MenuComponent>
    </div>
    <div class="content">
      <ContentComponent
        :isLogined1="isLogined"
        @loginSOF="loginOrNot">
      </ContentComponent>
    </div>
  </div>
  <div class="footer">
    <FooterComponent></FooterComponent>
  </div>
</template>

<script>
import HeaderComponent from './components/HeaderComponent.vue'
import MenuComponent from './components/MenuComponent.vue'
import ContentComponent from './components/ContentComponent.vue'
import FooterComponent from './components/FooterComponent.vue'

import userData from './assets/js/userData.js'
// import menuData from './assets/js/menu/menuData.js'

export default {
  name: 'App',
  components: {
    HeaderComponent,
    MenuComponent,
    ContentComponent,
    FooterComponent
  },
  data() {
    return {
      users : userData,
      menus : [],
      isLogined : false,
    }
  },
  mounted() {
    localStorage.setItem("users", JSON.stringify(this.users));
    
    const loginedUser = JSON.parse(localStorage.getItem("loginedUser"));
    const flag = loginedUser ? true : false;
    this.loginOrNot(flag);
  },
  methods : {
    goMain(flag) {
      if(!flag) this.$router.push("/vue-menu");
    },
    loginOrNot(flag) {
      this.isLogined = flag;
      this.getMenu();
      this.goMain(flag);
    },
    logoutFunc() {
      alert("로그아웃!");
      localStorage.removeItem("loginedUser");
      this.isLogined = false;
      this.getMenu();
      this.goMain(false);
    },
    getMenu() {
      let loginedUser = JSON.parse(localStorage.getItem("loginedUser"));
      const auth = (loginedUser != null) ? loginedUser.authority : "";

      // 로그인 권한에 따라 메뉴 불러오기
      switch(auth) {
        case "ADMIN":
          import("./assets/js/menu/menuData1.js")
            .then(obj => {this.menus = this.makeTreeMenu(obj.default)});
          break;
        case "MANAGER":
          import("./assets/js/menu/menuData2.js")
            .then(obj => {this.menus = this.makeTreeMenu(obj.default)});
          break;
        case "GUEST":
          import("./assets/js/menu/menuData3.js")
            .then(obj => {this.menus = this.makeTreeMenu(obj.default)});
          break;
        default:
          import("./assets/js/menu/menuData4.js")
            .then(obj => {this.menus = this.makeTreeMenu(obj.default)});
          break;
      }
    },
    makeTreeMenu(menu1) {
      let map = {}, treeMenu = [], i;
      for(i=0; i<menu1.length; i++) {
        map[menu1[i].name] = i;
        menu1[i].children = [];

        if(menu1[i].depth === 0) menu1[i].show = true;
        else menu1[i].show = false;
      }

      for(i=0; i<menu1.length; i++) {
        let node = menu1[i];
        if(node.parent !== "-") {
          menu1[map[node.parent]].children.push(node);
        } else {
          treeMenu.push(node);
        }
      }

      console.log(treeMenu);
      return treeMenu;
    },
  },
}
</script>

<style>
#app {
  margin-left: 10px;
  margin-right: 10px;
}

.header {
  border : 1px solid;
  min-height: 120px;
}

.footer {
  clear: both;
  border : 1px solid;
  height: 50px;
  background-color: lightgrey;
}

.middle {
  display: flex;
  min-height: 600px;
}

.menu {
  border : 1px solid;
  flex: 1;
  background-color : aliceblue;
}

.content {
  border : 1px solid;
  flex: 3;
}

</style>
