<template>
  <div style="padding-left: 10px; padding-top: 10px;">
    <!-- <div>
      헤더, 푸터, 메뉴를 제외한 내용들이 나오는 컴포넌트
    </div>
    <br/> -->
    <LoginComponent
        v-if="isLogined == false"
        @loginSOF="loginFunc">
    </LoginComponent>
    <MainComponent
        v-if="isLogined == true">
    </MainComponent>
  </div>
</template>

<script>

import LoginComponent from './LoginComponent.vue'
import MainComponent from './MainComponent.vue'

export default {
    name : "ContentComponent",
    components : {
        LoginComponent,
        MainComponent
    },
    props : {
        isLogined1 : Boolean,
    },
    data() {
        return {
            isLogined : false,
        }
    },
    mounted() {
        if(localStorage.getItem("loginedUser") != undefined && localStorage.getItem("loginedUser") != null) {
            this.loginFunc();
        }
    },
    methods : {
        loginFunc() {
            this.isLogined = true;
            this.$emit("loginSOF", this.isLogined);
        },
        logoutFunc() {
            alert("로그아웃!");
            localStorage.removeItem("loginedUser");
            this.isLogined = false;
            this.$emit("loginSOF", this.isLogined);
        },
    },
    watch : {
        isLogined1() {
            this.isLogined = this.isLogined1;
        },
    },
}
</script>

