import { createWebHistory, createRouter } from "vue-router";

const DEFAULT_PATH = "/vue-menu";
const routes = [
    {
        // '/'로 접근하더라도 디폴트 패스로 이동하게 하기
        path: '/',
        redirect: DEFAULT_PATH,
    },
    {
        path: DEFAULT_PATH + '/',
        name: 'main',
        component: () => import('@/views/MainPage.vue'),
    },
    {
        path: DEFAULT_PATH + '/menu1',
        name: 'menuOne',
        component: () => import('@/views/MenuOnePage.vue'),
    },
    {
        path: DEFAULT_PATH + '/admin1',
        name: 'admin1',
        component: () => import('@/views/AdminPageOne.vue'),
        beforeEnter: (to, from, next) => {
            const nowUser = JSON.parse(localStorage.getItem("loginedUser"));
            if(nowUser !== null && nowUser.authority === "ADMIN") {
                next();
            } else {
                next(DEFAULT_PATH);
            }
        },
    },
];

const router = new createRouter({
    history : createWebHistory(),
    routes : routes
});

export default router;