<template>
  <div>
    <div>
        <div v-for="(menu, index) in menus" :key="index">
            <!-- <div v-if="menu.show"> -->
            <div :style="paddingLeft" class="tooltip">
                <span @click="subMenuShow[index] = !subMenuShow[index]">{{menu.name}}</span>
                <span>&nbsp;</span>
                <span>&nbsp;</span>
                <button @click="menuGo(menu.url)">go1</button>
                <span class="tooltiptext tooltip-right">{{ menu.name }} 입니다. {{ menu.url }}</span>
            </div>
            <div v-if="menu.children.length > 0">
                <menu-component
                    v-if="subMenuShow[index]"
                    :menus="menu.children"
                    :isLogined="isLogined"
                    :padding="padding + 15">
                </menu-component>
            </div>
            <!-- </div> -->
        </div>
    </div>
  </div>
</template>

<script>
const default_path = "/vue-menu";

export default {
    name : "MenuComponent",
    props : {
        menus : Array,
        isLogined : Boolean,
        padding : Number,
    },
    data() {
        return {
            loginedUser : {},
            subMenuShow : [],
        }
    },
    mounted() {},
    methods : {
        menuGo(url) {
            if(url === undefined || url === null || url === '') return;
            this.$router.push(default_path + url);
        },
    },
    watch : {
        isLogined() {
            this.loginedUser = JSON.parse(localStorage.getItem("loginedUser"));
        },
        menus() {
            for(let i=0; i<this.menus.length; i++) this.subMenuShow.push(false);
        },
    },
    computed : {
        paddingLeft() {
            return {
                'margin-left' : `${this.padding}px`
            }
        },
    },
}
</script>

<style scoped>
.tooltip {
  position: relative;
  display: block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 190px;
  background-color: antiquewhite;
  color: black;
  text-align: center;
  border-radius: 5px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.tooltip .tooltip-right {
  top: -5px;
  left: 50%;
}

</style>