<template>
  <div>
    <div>
      == This is Header Component ==
    </div>

    <div v-if="isLogined == false">
      <div class="beforeInfo">
        로그인을 해야 정보가 나타납니다.
      </div>
    </div>
    <div v-else>
      <div class="headerInfo">
        <span class="loginInfo">{{loginedUser.name}} 님이 로그인 했습니다</span>
        <button v-if="loginedUser.authority === 'ADMIN'" @click="goAuthPage">관리자</button>
        <button @click="logoutClick">로그아웃</button>
      </div>
    </div>

    <div class="header-menu">
      <ul>
        <li class="dropdown" v-for="(menu, index) in menus" :key="index">
          <a>{{menu.name}}</a>
          <div v-if="menu.children.length > 0">
            <div class="dropdown-content" v-for="(sub, index1) in menu.children" :key="index1">
              <a>{{sub.name}}</a>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
const default_path = "/vue-menu"
export default {
    name : "HeaderComponent",
    props : {
      isLogined : Boolean,
      menus : Array,
    },
    data() {
      return {
        loginedUser : {},
      }
    },
    mounted() {},
    methods : {
      logoutClick() {
        this.$emit("logout");
      },
      goAuthPage() {
        this.$router.push(default_path + "/admin1");
      },
    },
    watch : {
      isLogined() {
        this.loginedUser = JSON.parse(localStorage.getItem("loginedUser"));
      },
    },
}
</script>

<style scoped>
.beforeInfo {
  text-align: center;
}

.headerInfo {
  float: right;
  padding-right: 10px;
}

.loginInfo {
  padding-right: 15px;
}

ul {
  list-style-type: none;
  margin : 0;
  padding : 0;
}

li {
  float : left;
  min-width: 110px;
}

.header-menu {
  display : flex;
}

.dropdown {
  border : 1px solid;
}

.dropdown-content {
  display: none;
  z-index: 10000000;
}

.dropdown:hover .dropdown-content {
  display : block;
}
</style>